
// 获取表格数据封装
export function getData(root,url,params,arr)
{
    root.service.get(url,{params:params || {}}).then((res)=>{
        if(res.data.status===200){
            console.log(res)
            root.tableData = res.data.data
            root.total = res.data.total
            // 数据转换
            root.tableData.map(item=>{
                arr.map(afield=>{
                    item[afield]?item[afield+'_text']='是':item[afield+'_text']='否'
                })
            })
            root.loading =false
        }
    })
}