<template>
    <div class="worklist">
        <el-table :data="tableData" border style="width: 100%" v-loading="loading">
            <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="userId" label="所属班级" align="center"></el-table-column>
            <el-table-column prop="title" label="作业名称" align="center"></el-table-column>
            <el-table-column prop="completed_text" label="作业完成情况" align="center"></el-table-column>
        </el-table>

        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
            :page-sizes="[10, 20, 40, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>
</template>
<script>
import {getData} from "@/utils/workTable.js"
export default{
    data(){
        return{
            tableData:[],
            currentPage: 1,//当前页数
            pageSize: 10,//每页显示数
            total:0,
            loading:true,
        }
    },
    methods:{
        getData(){
            getData(this,'/works',{page:this.currentPage,size:this.pageSize},['completed'])
        },
        handleCurrentChange(val){
            this.currentPage = val
            getData(this,'/works',{page:val,size:this.pageSize},['completed'])
        },
        handleSizeChange(val){
            this.pageSize = val
            this.currentPage = 1
            getData(this,'/works',{page:this.currentPage,size:val},['completed'])
        }
    },
    created(){
        this.getData()
    }
}
</script>

